.container {
  display: flex;
  align-items: center;
  padding: 2rem 0;
  min-height: calc(100vh - 76px);
  background-image: radial-gradient(#531116, #181c27);
  background-repeat: no-repeat;
  background-position: 50% 0%;
  background-size: cover;
  filter: blur(16px);
  transition: 1s;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2rem;
    margin-left: -1rem;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(23, 24, 27, 1));
  }

  &[data-loaded='true'] {
    filter: blur(0);
  }
}

.wrap {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.descriptor {
  max-width: 576px;

  & > h1 {
    font-size: 5rem;
    margin-top: 0;
  }

  & > p {
    color: lightgray;
  }
}

.ratingsRow {
  display: flex;
}

@media only screen and (max-width: 768px) {
  .container {
    padding: 4rem 1rem;
  }

  .descriptor {
    & > h1 {
      font-size: 2rem;
    }
  }
}

.link {
  margin-top: 1rem;
  color: inherit;

  &:hover {
    text-decoration: underline;
    color: #9997ff;
  }

  a {
    color: inherit;
    text-decoration: none;
    transition: 0.3s linear;
    margin-left: 0.5rem;
  }
}
